///////  PREVIEW PURPOSE 
@import "core/preview";

.nk-header-docs-nav{
    margin-left: auto;
}
.docs-nav{
    padding: .5rem 0;
    li{
        a{
            display: block;
            line-height: 1.25rem;
            padding: .5rem 0;
        }
    }
}
//
@media (min-width:992px){
    .nk-header-docs-nav{
        margin-left: auto;
        margin-right: auto;
    }
    .docs-nav{
        display: flex;
        padding: 0;
        margin: 0 -15px;
        li{
            padding: 0 15px;
            a{
                display: block;
                line-height: 1.25rem;
                padding: 1.375rem 0;
            }
        }
    }
    .dropdown-expand-lg{
        .dropdown-toggle{
            display: none;
        }
        .dropdown-menu{
            position: static;
            visibility: visible;
            display: block !important;
            box-shadow: none;
            border: none;
            margin: 0 !important;
            .overline-title{
                display: none;
            }
        }
    }
}
