// Content Aside
@keyframes animate-fade{
    0%{opacity: 0;}
    100%{opacity: 1;}
}
.nk-aside{
    position: fixed;
    &-header{
        display: none;
    }
    &-close{
        display: none;
    }
}

$aside-content-body-height-calc: ($content-gap-y + 8 + $header-height);
.has-aside {
    .nk-content{
        padding-bottom: 0;
        &-wrap{
            padding-bottom: 2rem;
        }
        &-body{
            display: flex;
            flex-direction: column;
            min-height: calc(100vh - #{$aside-content-body-height-calc});
        }
    }
    .nk-footer{
        border-top: 0;
        padding-left: 0;
        padding-right: 0;
        margin-left: -1rem;
        margin-right: -1rem;
    }
}
@include media-breakpoint-up(lg){
    .has-aside {
        .nk-content-inner{
            display: flex;
        }
        .nk-content-body{
            padding: 2rem 0 0;
        }
    }
    .nk-aside{
        padding: 2rem 0;
        width: 200px;
        flex-shrink: 0;
        position: static;
        background: transparent;
        opacity: 1;
        visibility: visible;
        margin-right: 48px;
    }
}
@include media-breakpoint-up(xl){
    .nk-aside{
        margin-right: 68px;
    }
}